
.MobileContainer .TdiJoin .hasCash {
  background: #f5f5f5;
}
.MobileContainer .TdiJoin .el-col {
  width: 50%;
  height: 40px;
  line-height: 40px;
  padding: 0 15px;
}
.MobileContainer .TdiJoin .el-col:nth-child(2n) {
  text-align: right;
}
.MobileContainer .TdiJoin .el-form,
.MobileContainer .TdiJoin .tdi-caution {
  padding: 0 15px;
}
.MobileContainer .TdiJoin .el-form {
  margin-top: 20px;
}
.MobileContainer .TdiJoin .el-form::after {
  content: '';
  display: block;
  clear: both;
}
.MobileContainer .TdiJoin .el-form .el-form-item {
  float: left;
  margin-bottom: 0;
}
.MobileContainer .TdiJoin .el-form .input {
  width: calc(100% - 60px);
}
.MobileContainer .TdiJoin .el-form .input .el-form-item__label {
  float: left;
  width: 60px;
  text-align: left;
  padding: 0;
}
.MobileContainer .TdiJoin .el-form .input .el-form-item__content {
  float: left;
  width: calc(100% - 60px);
}
.MobileContainer .TdiJoin .el-form .input .el-input__inner {
  font-size: 18px;
  color: #212121;
  text-align: right;
  border-radius: 0;
  border: 1px #d7d7d7 solid;
  border-right: 0 !important;
}
.MobileContainer .TdiJoin .el-form .input .el-input__inner::placeholder,
.MobileContainer .TdiJoin .el-form .input .el-input__inner::-webkit-placeholder {
  color: #212121;
}
.MobileContainer .TdiJoin .el-form .input .el-input__inner:focus {
  border-right: 0 !important;
}
.MobileContainer .TdiJoin .el-form .el-input-group__append {
  width: 40px;
  text-align: left;
  background: none;
  padding: 0;
  border-radius: 0;
  border: 1px #d7d7d7 solid;
  border-left: 0;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.MobileContainer .TdiJoin .el-form .input .el-input__inner:focus + .el-input-group__append {
  border: 1px #409EFF solid;
  border-left: 0;
}
.MobileContainer .TdiJoin .el-form .btn {
  width: 60px;
}
.MobileContainer .TdiJoin .el-form .btn .el-button {
  width: 60px;
  height: 40px;
  line-height: 40px;
  border-radius: 0;
  border: 1px #d7d7d7 solid;
  border-left: 0;
  background: #e5e5e5;
  padding: 0;
}
.MobileContainer .TdiJoin .change-value {
  text-align: right;
  padding-right: 15px;
}
.MobileContainer .TdiJoin .tdi-caution {
  font-size: 11px;
  color: #999;
}
.MobileContainer .TdiJoin .el-form .submit {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}
.MobileContainer .TdiJoin .el-form .submit .el-form-item__content {
  line-height: 1;
}
.MobileContainer .TdiJoin .el-form .submit .el-button {
  width: 100%;
}



