
.TdiCoinDetail {
    height:calc(100% - 60px);
}
.TdiCoinDetail .tdi-coin-detail {
    position:relative;
    height:calc(100% - 40px);
    overflow:auto;
    padding-bottom:15px;
}
.TdiCoinDetail .tdi-name {
    padding:0 15px;
}
.TdiCoinDetail .tdi-name img {
    vertical-align:middle;
    margin-right:5px;
}
.TdiCoinDetail .tdi-introduce,
.TdiCoinDetail .tdi-link,
.TdiCoinDetail .tdi-state {
    padding:0 15px;
    margin-top:15px;
}
.TdiCoinDetail .tdi-state .graph {
    height:80px;
}
.TdiCoinDetail .tdi-state li {
    padding-bottom:10px;
}
.TdiCoinDetail .tdi-state .label {
    font-weight:bold;
}
.TdiCoinDetail .coin-stitle {
    font-size:16px;
    font-weight:bold;
    margin-bottom:10px;
}
.TdiCoinDetail .tdi-summary {
    position:relative;
    margin-top:15px;
    padding:0 15px;
}
.TdiCoinDetail .tdi-summary dl::after {
    content:'';
    display:block;
    clear:both;
}
.TdiCoinDetail .tdi-summary dt,
.TdiCoinDetail .tdi-summary dd {
    float:left;
    padding-bottom:5px;
}
.TdiCoinDetail .tdi-summary dt {
    width:120px;
    color:#999;
}
.TdiCoinDetail .tdi-summary dd {
    width:calc(100% - 120px);
}
.TdiCoinDetail .coin-detail {
    padding:0 15px;
    margin-top:15px;
}
.TdiCoinDetail .bottomBtn {
    position:absolute;
    left:0;
    bottom:0;
    width:100%;
}
.TdiCoinDetail .bottomBtn .el-button {
    width:100%;
}
.TdiCoinDetail .bottomBtn .btm-upcomming {
    width:100%;
    height:40px;
    color:#fff;
    text-align:center;
    line-height:40px;
    background:#999;
}
.TdiCoinDetail .bottomBtn .btm-terminate {
    width:100%;
    height:40px;
    color:#fff;
    text-align:center;
    line-height:40px;
    background:#999;
}



