* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
button, textarea, select, input {
  font-family: Roboto, 'Noto Sans KR', sans-serif;
}
body {
  margin: 0px;
  padding: 0px;
  font-family: Roboto, 'Noto Sans KR', sans-serif;
  font-size: 14px;
  color: #212121;
  -webkit-font-smoothing: antialiased;
}
a {
  text-decoration: none;
  color: inherit;
}
:focus {
  box-shadow: none !important;
}

/* input box */
.el-input .el-input__inner {
  height: 30px;
  font-family: Roboto, 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #616161;
  border: 1px solid #e1dfe1;
  border-radius: 0;
  padding-left: 10px;
}
.el-input .el-input__inner:hover {
  border: 1px solid #C0C4CC !important;
}
.el-input .el-input__inner:focus {
  border: 1px solid #409EFF !important;
}
.el-input .el-input__inner::placeholder,
.el-input .el-input__inner::-webkit-placeholder,
.el-textarea textarea::placeholder,
.el-textarea textarea::-webkit-placeholder {
  color: #bdbdbd;
}
.el-textarea textarea {
  font-size: 14px;
  font-family: Roboto, 'Noto Sans KR', sans-serif;
  color: #616161;
  border: 1px solid #dcdfe6;
  border-radius: 0;
}

/* radio */
.el-radio__input.is-checked .el-radio__inner {
  width: 20px;
  height: 20px;
  background: #00e2aa !important;
  border-color: #00e2aa !important;
}
.el-radio__inner {
  width: 20px !important;
  height: 20px !important;
  border: 2px #e4e4e4 solid !important;
}
.el-radio__inner::after {
  width: 10px !important;
  height: 10px !important;
  background-color: #fff !important;
}
.el-radio__label {
  font-size: 12px !important;
  color: #616161 !important;
  padding-left: 10px;
}
.el-radio__input.is-checked + .el-radio__label {
  font-size: 12px;
  color: #000 !important;
}

/* checkbox */
.el-checkbox__inner {
  width: 20px !important;
  height: 20px !important;
  border: 2px #e4e4e4 solid !important;
  border-radius: 3px;
}
.el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner {
  border-color: #00e2aa !important;
  background-color: #00e2aa !important;
}
.el-checkbox__input.is-indeterminate .el-checkbox__inner::before {
  top: 7px !important;
}
.el-checkbox__label {
  font-size: 14px !important;
  font-weight: 400;
  padding-left: 8px !important;
  color: #616161 !important;
}
.el-checkbox__input.is-checked + .el-checkbox__label {
  color: #4b4b4b !important;
}
.el-checkbox__inner::after {
  top: 5px !important;
  left: 3px !important;
  border: 0 !important;
  width: 10px !important;
  transform: none !important;
  background: url("../images/ico_check.png") no-repeat;
}
.el-checkbox__input.is-disabled .el-checkbox__inner {
  background-color: #eee !important;
  border-color: #e0e0e0 !important;
}
.el-checkbox__input.is-disabled .el-checkbox__inner::after {
  transform: none !important;
  background: url("../images/ico_check_disabled.png") no-repeat;
}
.el-checkbox__input.is-disabled + span.el-checkbox__label {
  color: #dbdbdb !important;
}

/* select box */
.el-select > .el-input {
  display: inline-block !important;
  line-height: initial;
}
.el-select .el-input__inner {
  border: 1px #e0e0e0 solid;
  border-radius: 0;
}
.el-select .el-input__icon {
  width: 10px;
}
.el-select .el-input__suffix {
  right: 15px !important;
}
.el-select .el-input__suffix-inner {
  display: block;
  height: 100%;
}
.el-select .el-icon-arrow-up:before {
  content: '' !important;
  display: block;
  width: 100%;
  height: 100%;
  background: url("../images/arrow_down.png") center no-repeat;
  transform: rotateZ(180deg);
}
.el-select .el-input__icon::after {
  display: none;
}

/* select dropdown */
.el-select-dropdown,
.el-dropdown-menu,
.el-autocomplete-suggestion,
.el-picker-panel {
  margin-top: 0 !important;
  border-radius: 0 !important;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.05) !important;
}
.el-dropdown-menu {
  padding: 5px 0 !important;
}
.el-dropdown-menu .popper__arrow,
.el-select-dropdown .popper__arrow,
.el-autocomplete-suggestion .popper__arrow,
.el-picker-panel .popper__arrow {
  display: none !important;
}
.el-select-dropdown__item.selected,
.el-date-table td.today span,
.el-date-table td.available:hover {
  color: #304ffe !important;
}
.el-date-table td.end-date span,
.el-date-table td.start-date span {
  background: #3f51b5 !important;
}
.el-date-table td.today.end-date span,
.el-date-table td.today.start-date span {
  color: #fff !important;
}
.el-dropdown-menu__item,
.el-select-dropdown__item {
  font-family: Dotum;
  color: #606266 !important;
  font-size: 12px !important;
  padding: 0 15px !important;
}
.el-dropdown-menu__item:hover {
  background: #f5f7fa !important;
}

