    border-right: none;
    border-top: 1px #f0f2f7 solid;
}
.PcContainer .Header + .el-container > .el-aside > .el-menu > .el-menu-item {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px #f0f2f7 solid;
