    content: '' !important;
    display: block;
    width: 20px;
    height: 20px;
    background: url("../images/m_popup_close.png") no-repeat;
    background-size: 100%;
}
.PcContainer .el-dialog .el-dialog__title {
    font-size: 21px;
    color: #212121;
    text-align: center;
}
.PcContainer .el-dialog .order-dialog-contents {
    margin: 8px 0;
}
.PcContainer .el-dialog .order-dialog-contents:first-child {
    margin-top: 0;
}
.PcContainer .el-dialog .order-dialog-contents:last-child {
    margin-bottom: 0;
}
.PcContainer .el-dialog .el-dialog__footer {
    padding: 30px 40px;
    text-align: center !important;
}
.PcContainer .el-dialog .el-dialog__footer .el-button {
    width: 80px !important;
    height: 30px !important;
    padding: 0 !important;
    border: 0 !important;
    border-radius: 2px !important;
}
.PcContainer .el-dialog .el-dialog__footer .el-button--info,
  .PcContainer .el-dialog .el-dialog__footer .el-button--default {
    color: #212121 !important;
    background: #E0E0E0 !important;
}
.PcContainer .el-dialog .el-dialog__footer .el-button--primary {
    color: #fff !important;
    background: #3F51B5 !important;
}
.el-menu--horizontal {
  border-bottom: 0 !important;
}

/* table sorting */
.el-table .caret-wrapper {
  position: absolute !important;
  width: 16px !important;
  height: 100% !important;
}
.el-table .sort-caret {
  left: calc(50% - 5px) !important;
  border: 0 !important;
  width: 7px !important;
  height: 10px !important;
}
.el-table .sort-caret.ascending {
  top: calc(50% - 6px) !important;
  background: url("../images/ico_descending.png") no-repeat;
  transform: rotateZ(180deg);
  visibility: hidden;
}
.el-table .sort-caret.descending {
  bottom: calc(50% - 4px) !important;
  background: url("../images/ico_descending.png") no-repeat;
  visibility: hidden;
}
.el-table .descending .sort-caret.descending {
  visibility: visible;
}
.el-table .ascending .sort-caret.ascending {
  visibility: visible;
}

/* pop-over */
.el-popover {
  font-weight: 300 !important;
  border: 0 !important;
  padding: 40px !important;
  color: #616161 !important;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1) !important;
}
.el-popover__title {
  font-weight: 400;
  font-size: 16px;
  color: #212121;
}
.el-popover h2 {
  font-size: 16px;
  font-weight: normal;
  color: #212121;
  line-height: 1;
  padding: 0;
  margin-top: 0;
}
.el-popover p {
  color: #616161;
  padding: 0;
  margin-top: 15px;
}
.el-popover p + h2 {
  margin-top: 25px;
}
.el-popover ul {
  margin-top: 15px;
}
.el-popover li {
  position: relative;
  padding: 5px 0 0 10px;
}
.el-popover .dot::before {
  content: '';
  position: absolute;
  top: 12px;
  left: 0;
  width: 4px;
  height: 4px;
  background: #9e9e9e;
}
.el-popover .dash::before {
  content: '-';
  position: absolute;
  left: 0;
  color: #616161;
}
.el-popover .etc::before {
  content: '※';
  position: absolute;
  left: -3px;
  font-size: 12px;
  color: #616161;
}
.el-popover .caution {
  font-weight: 400;
  font-size: 16px;
  color: #ff1744;
}
.el-popover dl.reload-select dd {
  margin: 0;
  margin-bottom: 10px;
}
.el-popover dl.reload-select dd + dd {
  margin-bottom: 0;
}
.el-popover ul.strategy-period {
  border-top: 1px #ebeef5 solid;
}
.el-popover ul.strategy-period li {
  border-bottom: 1px #ebeef5 solid;
  padding: 12px 0;
  cursor: pointer;
}
.el-popover ul.strategy-period li:hover {
  background: #f5f5f5;
}
.el-popover ul.strategy-period li.isSel,
.Screener .menu li .el-button.isSel {
  background: #f5f5f5;
}
.el-popover .popover-btn {
  margin-top: 20px;
}
.el-popover .popover-btn .el-button {
  width: 100%;
}
.el-popover .info h2 {
  font-size: 14px;
  color: initial;
  margin-top: 0;
  margin-bottom: 10px;
}
.el-popover .info p {
  margin-top: 0;
  margin-bottom: 10px;
}
.el-popover .info p:last-child {
  margin-bottom: 0;
}

/* pc 팝업 - alert */
@media screen and (min-width: 1024px) {
.el-message-box {
    width: 400px !important;
    font-family: Roboto, 'Noto Sans KR', sans-serif;
    font-weight: 300;
    border-radius: 0 !important;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1) !important;
    border: 0 !important;
    padding: 0 !important;
}
.el-message-box .el-message-box__header {
    font-size: 22px;
    color: #212121;
    padding: 40px 40px 20px !important;
    text-align: left;
    line-height: 1;
}
.el-message-box .el-message-box__header {
    padding-top: 45px !important;
}
.el-message-box .el-message-box__content {
    padding: 0 40px !important;
}
.el-message-box .el-message-box__content {
    text-align: center;
}
.el-message-box .el-message-box__headerbtn {
    top: 3px;
    right: 3px;
    width: 40px;
    height: 40px;
}
.el-message-box .el-message-box__headerbtn .el-icon-close {
    color: #212121;
    font-size: 36px;
    font-weight: 300;
}
.el-message-box .el-message-box__title {
    font-size: 21px;
    color: #212121;
    text-align: center;
}
.el-message-box .alert-cont {
    text-align: left;
}
.el-message-box .alert-cont::after {
    content: '';
    display: block;
    clear: both;
}
.el-message-box .alert-cont span {
    float: left;
    display: block;
    line-height: 30px;
}
.el-message-box .alert-cont .label {
    width: 100px;
}
.el-message-box .alert-cont .value {
    width: calc(100% - 100px);
    text-align: right;
}
.el-message-box .alert-cont .text {
    width: 100%;
}
.el-message-box .el-message-box__btns {
    padding: 30px 40px;
    text-align: center !important;
}
.el-message-box .el-message-box__btns .el-button {
    width: 80px !important;
    height: 30px !important;
    padding: 0 !important;
    border: 0 !important;
    border-radius: 2px !important;
}
.el-message-box .el-message-box__btns .el-button--default {
    color: #212121 !important;
    background: #E0E0E0 !important;
}
.el-message-box .el-message-box__btns .el-button--primary {
    color: #fff !important;
    background: #3F51B5 !important;
}
  /* 자동완성 */
.el-autocomplete-suggestion .address::after {
    content: '';
    display: block;
    clear: both;
}
.el-autocomplete-suggestion .address .value {
    float: left;
    width: 150px;
}
.el-autocomplete-suggestion .address .link {
    float: left;
    width: calc(100% - 150px);
    padding-right: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.el-autocomplete-suggestion .address .delete {
    width: 14px;
    height: 14px;
    border: 0;
    padding: 0;
}
.el-autocomplete-suggestion .address-btn {
    text-align: center;
    margin-top: 20px;
}
.el-autocomplete-suggestion .address-btn .el-button {
    width: 140px;
    height: 30px;
    color: #616161;
    border: 1px #e0e0e0 solid;
    border-radius: 2px;
    padding: 0;
}
.el-autocomplete-suggestion .address-btn .el-button:hover {
    border-color: #9e9e9e;
    background: #f5f5f5;
}
.el-autocomplete-suggestion.my-autocomplete .el-autocomplete-suggestion__list li:last-child {
    cursor: default;
}
.el-autocomplete-suggestion.my-autocomplete .el-autocomplete-suggestion__list li:last-child:hover {
    background: none;
}
  /* tooltip */
.el-tooltip__popper {
    border-radius: 2px !important;
    background: rgba(48, 49, 51, 0.8) !important;
}
.el-tooltip__popper[x-placement^=left] .popper__arrow {
    border-left: 0 !important;
}
.el-tooltip__popper[x-placement^=left] .popper__arrow::after {
    border-left-color: rgba(48, 49, 51, 0.8) !important;
}
}



